






//将任意目录中.svg结尾的文件引入到当前文件中，不用一个一个引入
const importAll = (requireContext: __WebpackModuleApi.RequireContext) => requireContext.keys().forEach(requireContext)
try{
    importAll(require.context('../assets/icons',true,/\.svg$/))
}catch(error){
    console.log(error);

}

export default {
    props:['name'],
    name :'Icon'
}
