






















import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tabs from '@/components/Tabs.vue';
import recordTypeList from '@/components/constents/recordTypeList';
import  dayjs from 'dayjs';
import clone from '@/lib/clone';

@Component({components:{Tabs}})
export default class Statistic extends Vue{
    tagString(tags: Tag[]){
        return tags.length === 0 ?'无' : tags.map(t=> t.name).join('，')
    }
    get recordList(){
        return (this.$store.state as RootState).recordList;
    }
    beautify(string: string){
        const day = dayjs(string)
        const now = dayjs();
        if(day.isSame(now,'day')){
            return '今天';
        }else if(day.isSame(now.subtract(1,'day'),'day')){
            return '昨天';
        }else if(day.isSame(now.subtract(2,'day'),'day')){
            return '前天';
        }else if(day.isSame(now,'year')){
            return day.format('M月D日')
        }else{
            return day.format('YYYY年MM月DD日')
        }
    }
    get result(){
        const {recordList} = this;
        const newList = clone(recordList).filter(r => r.type === this.type).sort((a,b)=> dayjs(b.createAt).valueOf() - dayjs(a.createAt).valueOf())
        type GroupedList ={title: string;total?: number;items: RecordItem[]}[]
        if(newList.length === 0){return [] as GroupedList}
        const groupedList: GroupedList = [{title: dayjs(newList[0].createAt).format('YYYY-MM-DD'),items:[newList[0]]}];
        for(let i = 1;i < newList.length; i++){
            const current =  newList[i]
            const last = groupedList[groupedList.length - 1]
            if(dayjs(last.title).isSame(dayjs(current.createAt),'day')){
                last.items.push(current)
            }else{
                groupedList.push({title: dayjs(current.createAt).format('YYYY-MM-DD'),items:[current]})
            }
        }
        groupedList.map(group =>{
            group.total = group.items.reduce((sum,item)=> sum + item.amount,0)
        })
        return groupedList
    }
    beforeCreate(){
        this.$store.commit('fetchRecords')
        // this.isShow()
    }
    // isShow(){
    //     if(this.recordList.length === 0){
    //         return this.isText = false
    //     }else{
    //         return  this.isText = true
    //
    //
    //     }
    // }
    type='-'
    recordTypeList= recordTypeList
}
